const emailInput = <HTMLInputElement>document.getElementById("contact-email");
const emailConfInput = <HTMLInputElement>(
  document.getElementById("contact-email-conf")
);
const contactSubmitBtn = <HTMLButtonElement>(
  document.getElementById("contact-submit")
);
emailConfInput.addEventListener("input", (e) => {
  const emailValue = emailInput.value;
  if (!(e.target instanceof HTMLInputElement)) {
    return;
  }
  if (emailValue !== e.target.value) {
    emailConfInput.classList.add("inValid");
    contactSubmitBtn.setAttribute("disabled", "true");
  } else {
    emailConfInput.classList.remove("inValid");
    contactSubmitBtn.removeAttribute("disabled");
  }
});
